import { Link, useLocation, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import dayjs from "dayjs";
import { useFinanceReceipt } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { admissionModifyReceiptData } from "../../../../pdf/function/pdf-convertor-function";
import style from "../TransactionHistory/TransactionHistory.module.css";
const FinanceActivityLogsCard = ({ logs }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [receiptId, setReceiptId] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [downloadableData, setDownloadableData] = useState("");

  const { financeReceipt, financeReceiptRefetch } = useFinanceReceipt({
    rid: receiptId,
    skip: !receiptId,
  });

  useEffect(() => {
    if (receiptId) financeReceiptRefetch();
  }, [receiptId, financeReceiptRefetch]);
  useEffect(() => {
    if (financeReceipt?.receipt && financeReceipt?.all_remain) {
      setDownloadableData(financeReceipt);
    }
  }, [financeReceipt]);

  useEffect(() => {
    if (downloadableData) {
      const workerInstance = new Worker(
        new URL(
          "../../../../pdf/worker/main.receipt.pdf.worker.js",
          import.meta.url
        )
      );

      const { institute, studentInfo, paymentReceiptInfo } =
        admissionModifyReceiptData(
          downloadableData?.all_remain,
          downloadableData?.receipt
        );
      workerInstance.postMessage({
        institute,
        studentInfo,
        paymentReceiptInfo,
        printReceiptType:
          paymentReceiptInfo?.transactonRefund === "Refunded"
            ? "ADMISSION_REFUND"
            : "ADMISSION",
      });

      workerInstance.onmessage = (message) => {
        if (message) {
          window.open(message.data, "_blank");
          setDisabled((pre) => !pre);
          setDownloadableData("");
          workerInstance.terminate();
        }
      };
    }
  }, [downloadableData]);
  const onPdfDownload = () => {
    if (logs?.fee_receipt?._id) {
      setDisabled((pre) => !pre);
      setReceiptId(logs?.fee_receipt?._id);
      if (receiptId === logs?.fee_receipt?._id) {
        setDownloadableData(financeReceipt);
      }
    }
  };
  const onFeeReceipt = () => {
    onPdfDownload();
  };

  return (
    <>
      <div
        className={style.sidebarheader}
        style={{
          gap: "1%",
        }}
        title={"View Receipt"}
      >
        <div
          className={style.history_image_container}
          style={{
            width: "50%",
          }}
        >
          {logs?.fee_receipt?.student?.studentProfilePhoto ? (
            <Link
              to={`/q/${params.username}/student/profile`}
              state={{
                ...getQuery.state,
                sid: logs?.fee_receipt?.student?._id,
                // studentId: logs?.payment_student?._id,
              }}
            >
              <img
                src={`${imageShowUrl}/${logs?.fee_receipt?.student?.studentProfilePhoto}`}
                alt="qr code"
                style={{
                  width: "60px",
                  height: "60px",
                  borderRadius: "12px",
                }}
              />
            </Link>
          ) : null}

          <div className={style.text}>
            <h6>
              {`${logs?.fee_receipt?.student?.studentFirstName ?? ""} ${
                logs?.fee_receipt?.student?.studentMiddleName ?? ""
              } ${logs?.fee_receipt?.student?.studentLastName ?? ""}`}
            </h6>
            <p className={style.rcv}>
              {t("gr_number")} {logs?.fee_receipt?.student?.studentGRNO ?? ""}
            </p>
            <p className={style.rcv}>
              {dayjs(logs?.created_at)?.format("DD/MM/YYYY")}
              {", "}
              {localTimeFormat(logs?.created_at, "LT")}
            </p>
          </div>
        </div>

        <div
          className={style.text}
          style={{
            width: "27%",
          }}
        >
          <p
            className={style.rcv}
            style={{
              width: "100%",
            }}
          >
            {t("transaction_id")}
          </p>
          <h6
            style={{
              width: "100%",
              wordWrap: "break-word",
            }}
          >
            {logs?.fee_receipt?.razorpay_payment_id
              ? logs?.fee_receipt?.razorpay_payment_id
              : logs?.fee_receipt?.fee_utr_reference
              ? logs?.fee_receipt?.fee_utr_reference
              : "N/A"}
          </h6>
        </div>
        <div
          className={style.text}
          style={{
            width: "20%",
          }}
        >
          <p
            className={style.rcv}
            style={{
              width: "100%",
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            {t("invoice_no")} {logs?.fee_receipt?.invoice_count ?? "N/A"}
            <img
              src={`${assestsUserFinanceUrl}/receipt.svg`}
              alt="receipt icon"
              title="Download Fees Receipt"
              onClick={onFeeReceipt}
            />
          </p>

          <h6
            style={{
              width: "100%",
            }}
          >
            {t("rs_only")} {logs?.fee_receipt?.fee_payment_amount}
          </h6>
          <p
            className={style.rcv}
            style={{
              width: "100%",
            }}
          >
            {logs?.fee_receipt?.fee_payment_mode}
          </p>
        </div>
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default FinanceActivityLogsCard;
